<template>
  <ApolloQuery
    :query="require('../graphql/queries/getAdminProfile.gql')"
    class="row ma-sm-4"
  >
    <template v-slot="{ result: { error, data }, isLoading }">
      <!-- Loading -->
      <div
        v-if="isLoading === 1"
        class="mx-auto"
      >
        <v-progress-circular
          indeterminate
          color="primary"
        />
      </div>

      <!-- Error -->
      <AppNotification
        v-else-if="error"
        :message="error.message"
      />

      <!-- Result -->
      <template v-else-if="data">
        <v-col v-if="!data.getAdminProfile.permissions.includes('USERS_ADMINISTRATION') && data.getAdminProfile.role !== 'SUPERUSER'">
          <v-alert
            outlined
            type="error"
          >
            Этот раздел вам недоступен!
          </v-alert>
        </v-col>

        <template v-else>
          <v-col cols="12">
            <v-card>
              <v-card-text>
                <v-row>
                  <v-col
                    cols="12"
                    sm="6"
                  >
                    <v-list-item two-line>
                      <v-list-item-content>
                        <v-list-item-title>User ID</v-list-item-title>
                        <v-list-item-subtitle>1</v-list-item-subtitle>
                      </v-list-item-content>
                    </v-list-item>
                    <v-list-item two-line>
                      <v-list-item-content>
                        <v-list-item-title>Дата регистрации</v-list-item-title>
                        <v-list-item-subtitle>26.10.2020</v-list-item-subtitle>
                      </v-list-item-content>
                    </v-list-item>
                    <v-list-item two-line>
                      <v-list-item-content>
                        <v-list-item-title>Контактные данные</v-list-item-title>
                        <v-list-item-subtitle>136272838</v-list-item-subtitle>
                      </v-list-item-content>
                    </v-list-item>
                    <v-list-item two-line>
                      <v-list-item-content>
                        <v-list-item-title>Привязанные соц. сети</v-list-item-title>
                        <v-list-item-subtitle>Vk, Facebook</v-list-item-subtitle>
                      </v-list-item-content>
                    </v-list-item>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                  >
                    <v-text-field
                      v-model="username"
                      outlined
                      hide-details="auto"
                      label="Имя"
                      class="rounded-lg mb-5"
                    />
                    <v-text-field
                      v-model="password"
                      outlined
                      :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                      :type="show ? 'text' : 'password'"
                      hide-details="auto"
                      label="Пароль"
                      class="rounded-lg mb-5"
                      @click:append="show = !show"
                    />
                    <v-select
                      v-model="role"
                      :items="[]"
                      outlined
                      hide-details="auto"
                      label="Роль"
                      class="rounded-lg mb-5"
                    />
                    <v-select
                      v-model="permissions"
                      :items="permissionsItems"
                      outlined
                      hide-details="auto"
                      label="Права"
                      multiple
                      class="rounded-lg mb-5"
                    >
                      <template v-slot:selection="{ item, index }">
                        <v-chip v-if="index === 0">
                          <span>{{ item }}</span>
                        </v-chip>
                        <span
                          v-if="index === 1"
                          class="grey--text text-caption"
                        >
                          (+{{ permissions.length - 1 }} others)
                        </span>
                      </template>
                    </v-select>
                    <v-btn
                      block
                      depressed
                      color="primary"
                      class="text-none rounded-lg"
                      x-large
                    >
                      Сохранить
                    </v-btn>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-card>
          </v-col>

          <v-col
            cols="12"
            class="text-center text-h5"
          >
            История ставок
          </v-col>

          <v-col cols="12">
            <v-row>
              <v-col
                cols="12"
                sm="6"
              >
                <v-select
                  v-model="mode"
                  :items="[]"
                  solo
                  flat
                  hide-details="auto"
                  clearable
                  background-color="#464646"
                  label="Режим"
                  class="rounded-lg"
                  @change="firstPage"
                />
              </v-col>
              <v-col
                cols="12"
                sm="6"
              >
                <v-menu
                  ref="menu"
                  v-model="menu"
                  :close-on-content-click="false"
                  :return-value.sync="dates"
                  transition="scale-transition"
                  offset-y
                  min-width="290px"
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      v-model="dateRangeText"
                      solo
                      flat
                      hide-details="auto"
                      background-color="#464646"
                      label="Диапазон дат"
                      readonly
                      class="rounded-lg"
                      v-on="on"
                    >
                      <template v-slot:prepend-inner>
                        <v-icon class="mr-1">
                          mdi-calendar
                        </v-icon>
                      </template>
                    </v-text-field>
                  </template>
                  <v-date-picker
                    v-model="dates"
                    no-title
                    scrollable
                    range
                  >
                    <v-spacer />

                    <v-btn
                      text
                      color="primary"
                      @click="menu = false"
                    >
                      Cancel
                    </v-btn>
                    <v-btn
                      text
                      color="primary"
                      @click="saveDates"
                    >
                      OK
                    </v-btn>
                  </v-date-picker>
                </v-menu>
              </v-col>
            </v-row>
          </v-col>

          <v-col>
            <v-card
              flat
              class="pa-4 pb-2"
            >
              <v-data-table
                :headers="headers"
                :items="[]"
                :items-per-page="5"
                disable-sort
                no-data-text="Ставки отсутствуют"
                no-results-text="Ничего не найдено"
                hide-default-footer
              >
                <template v-slot:[`item.createdAt`]="{ item }">
                  {{ item.createdAt | moment('DD.MM.YYYY, HH:mm') }}
                </template>
              </v-data-table>
              <v-divider />

              <div class="text-center pt-3">
                <v-pagination
                  v-model="page"
                  :total-visible="9"
                  :length="0"
                />

                <v-select
                  v-model="itemsPerPage"
                  :items="[5, 10, 15]"
                  label="Ставок на странице"
                  class="pa-4 pt-5"
                  @change="firstPage"
                />
              </div>
            </v-card>
          </v-col>
        </template>
      </template>

      <!-- No result -->
      <AppNotification
        v-else
        message="Нет результата :("
      />
    </template>
  </ApolloQuery>
</template>

<script>
import AppNotification from '@/components/AppNotification.vue';

export default {
  components: {
    AppNotification,
  },
  data() {
    return {
      username: '',
      password: '',
      show: false,
      role: '',
      permissions: [],
      permissionsItems: ['DASHBOARD_ADMINISTRATION', 'CHAT_ADMINISTRATION', 'BONUS_ADMINISTRATION', 'USERS_ADMINISTRATION', 'WITHDRAW_ADMINISTRATION', 'GAMES_ADMINISTRATION'],
      headers: [
        { text: 'ID:', value: 'id', class: 'white--text text-subtitle-2' },
        { text: 'Режим:', value: 'mode', class: 'white--text text-subtitle-2' },
        { text: 'Дата:', value: 'createdAt', class: 'white--text text-subtitle-2' },
        { text: 'Результат:', value: 'result', class: 'white--text text-subtitle-2' },
      ],
      page: 1,
      itemsPerPage: 5,
      mode: '',
      menu: false,
      dates: [],
      firstDate: '',
      lastDate: '',
    };
  },
  computed: {
    dateRangeText() {
      return this.dates.join(' ~ ');
    },
    startDate() {
      return this.$moment(this.firstDate).unix();
    },
    finishDate() {
      if (this.lastDate) {
        return this.$moment(this.lastDate).unix() + 86399;
      }

      return this.$moment(this.lastDate).unix();
    },
  },
  methods: {
    firstPage() {
      this.page = 1;
    },
    saveDates() {
      this.$refs.menu.save(this.dates);
      [this.firstDate, this.lastDate] = this.dates;
    },
  },
};
</script>
